
.container{
    margin: 0 20px;
    position: relative;
}

@media (min-width: 768px) {
    .container{
        margin: 0 34px;
    }
}

@media (min-width: 1024px) {
    .container{
        margin: 0 42px;
    }
}

@media (min-width: 1366px) {
    .container{
        margin: 0 123px;
    }
}